.Features {
  background-color: #EEEEEE;
  padding: 3rem 0;

  text-align: center;

  p {
    color: hsl(0, 0%, 75%);
  }

  @media (min-width: 1024px) {
    padding: 6rem 0;
  }
}
.title {
  margin-top: 0;

  @media (min-width: 1024px) {
    margin-bottom: 0;
    font-size: 2.5rem;
  }
}
.description {
  margin-bottom: 5rem;
  max-width: 540px;
  margin-left: auto;
  margin-right: auto;
}

.items {
  display: flex;
  flex-direction: column;
  gap: 6rem;

  @media (min-width: 1024px) {
    flex-direction: row;
    justify-content: space-between;
    gap: 2rem;
    align-items: flex-start;
  }
}

.item {
  position: relative;
  z-index: 2;
  background-color: #fff;
  border-radius: 0.5rem;
  padding: 5rem 1rem 1.25rem;

  figure {
    position: absolute;
    z-index: 4;
    left: 50%;
    top: 0;
    margin: 0;
    transform: translate(-50%, -50%);

    background-color: hsl(257, 27%, 26%);;

    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1.5rem;
    border-radius: 50%;

    @media (min-width: 1024px) {
      left: 2rem;
      transform: translate(0%, -50%);
    }
  }

  h3 {
    margin-top: 0;
  }

  p {
    font-size: 14px;
  }

  &:not(:last-child)::after {
    content: '';

    display: block;
    position: absolute;
    bottom: -5rem;
    left: calc(50% - 4px);

    width: 8px;
    height: 5rem;
    background-color: hsl(180, 66%, 49%);
    z-index: 2;
  }

  @media (min-width: 1024px) {
    padding: 4rem 1rem 1.25rem;

    &:nth-child(1) {
      &::after {
        left: unset;
        right: -1.25rem;
        bottom: 30%;
        transform: rotate(90deg);
        height: 2rem;
      }
    }

    &:nth-child(2) {
      margin-top: 3rem;

      &::after {
        left: unset;
        right: -1.25rem;
        bottom: 50%;
        transform: rotate(90deg);
        height: 2rem;
      }
    }
    &:nth-child(3) {
      margin-top: 6rem;
    }
  }
  .container {
    width: 100%;
    max-width:800px;
    padding-left: 1.5rem;
    padding-right: 1.5rem;
    margin: 0 auto;
  }

  @media (min-width: 1024px) {
    .container {
      padding-left: 0;
      padding-right: 0;
    }
  }
}